import React, { Component } from 'react';
import '../../css/stylesheet.scss';
import Slider from './slider/Slider';
import Card from './Card';
import Discounts from './discountsComp/discounts';

const Container = ({ cardInfo, phones }) => {

  return (
    <div className="container-comp-css">
      <Slider/>
      <div className="goods">
        <div className="showCards">
          {cardInfo.map((section, i) => (
              <Card
                key={section.key}
                section={section}
              />
          ))}
        </div>
        <Discounts
          key={phones}
          phones={phones}
        />
      </div>
    </div>
  );
}

export default Container;
